import cartcontrol from '@/components/cartcontrol.vue'
import uniRate from '../../components/uni-rate.vue'
export default {
	data() {
		return {
			imgUrl: 'http://106.15.194.58/images/', //图片接口
			footData: {},
			flag: false,
			id: '',
			dayId: '',
			food: {
				"name": "小米粥",
				"price": 9.8,
				"oldPrice": "",
				"description": "食材：小米",
				"sellCount": 239,
				"img": '../../static/xiaomi.jpg',
				count: 0,
			},
		}
	},
	components: {
		cartcontrol,
		uniRate
	},
	onLoad(e) {
		// console.log(e)
		this.id = e.id
		this.dayId = e.dayId
		this.init(e.id, e.dayId)
	},
	methods: {
		getLevel() { // 评星
			// console.log(this.footData.level)
			return this.footData.level
		},
		init(id, dayId) {
			let that = this
			uni.showLoading({
				mask: true
			})
			uni.request({
				url: this.nowUrl + '/foods/find',
				header: {
					'token': this.token
				},
				data: {
					id: id,
					weekId: dayId
				},
				success(res) {
					// console.log(res)
					that.footData = res.data.data
					that.flag = true
					// console.log(that.footData)
					uni.hideLoading()
				}
			})
		},
		addCart: function(item) { // 加
			// console.log('ev', JSON.stringify(item))
			if (item.count >= 0) {
				item.count++
				this.food.count = item.count
			} else {
				//	console.log('add')
			}
		},
		decreaseCart(item) { //减
			if (item.count) {
				item.count--
				this.food.count = item.count
			}
		},
		moreAssess(id) { // 更多评价
			console.log(id)
			uni.navigateTo({
				url: '../moreAssess/moreAssess?id=' + this.id + '&dayId=' + this.dayId,
				animationType: 'slide-in-right',
				animationDuration: 200
			})
		}
	}
}
